.App {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
    /* color: #f2ff3d; */
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.line {
    border: 1px solid black;
    margin: 20px;
    width: 700px;
}

.http {
    color: red;
}

.grid {
    padding-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 20px;
}